.spinner-container {
  align-items: center;
  gap: 20px;
  display: flex;
  position: fixed;
  top: 30px;
  left: 20px;
}

.loading {
  font-size: large;
}

.loader {
  width: 32px;
  height: 32px;
  box-sizing: border-box;
  border: 8px solid #afaeae;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  padding-top: 60px;
  display: flex;
}

img {
  width: 300px;
  height: auto;
}

.text {
  max-width: 500px;
  display: inline-block;
}

.hidden {
  display: none;
}

